<template>
  <div>
    <div class="form-inline">
	    	
	  <div class="form-group">     
		   <input class="span2 form-control" type="search" v-model="goods.goodsname" placeholder="" style="width:400px" v-on:keyup.enter="getgoods">	      
	  </div>
	  
	  <button type="button" class="btn btn-default" @click='getgoods'></button>
	 <!-- <button type="button" class="btn btn-default" id="pickfiles"></button>-->
	  <!--<input type="file" id="pickfiles" class="hide" name="pickfiles"/>-->
	</div>
	<div class="goodstag">
    		<label for=""></label>
    		<span v-for="item in tagnames" @click="selected(item.tagname,item.name)" v-bind:class="{active: activeName == item.tagname}">{{item.tagname}}</span>
    	</div>
    	<div class="goodsort">
		<form class="form-inline">
			
			<ul>
				<label for=""></label><li v-for="item in sortarray" @click="sortselected(item.name,item.type)" v-bind:class="{active: sortName == item.name}">{{item.name}}</li>
			</ul>
		</form>
	</div>
	<div class="goodscontent">
		<template v-if="goodsdata.length">
			<table class="table table-bordered">
			   <thead>
				   	<tr>
				   		<th width="80"></th><th width="200"></th><th></th><th></th><th></th><th></th>
				   		<th></th><th></th><th width="80"></th><th width="80"></th><th width="80"></th><th></th><th></th><th></th>
			   		</tr>
			   </thead>
			   <!--<tbody>-->
			   	<tbody v-for="item in goodsdata">
				   	<tr>
				   		<td><a v-bind:href="item.auctionUrl" target="_blank">{{item.shopTitle}}</a></td><td><a v-html='item.title' v-bind:href="item.shortLinkUrl" target="_blank"></a></td><td><img v-bind:src="item.pictUrl" alt="" /></td>
				   		<td>{{item.reservePrice}}</td><td>{{item.zkPrice}}</td><td>{{item.biz30day}}</td><td>{{item.tkCommFee*0.5}}</td><td>{{item.taoToken}}</td>
				   		<td><p><a v-bind:href="item.shortLinkUrl" target="_blank">{{item.shortLinkUrl}}</a></p></td><td><img v-bind:src="item.qrCodeUrl"></td><td><p><a v-bind:href="item.couponLink" target="_blank">{{item.couponInfo}}</a></p></td><td>{{item.couponLinkTaoToken}}</td>
				   		<td>{{item.couponEffectiveStartTime}}</td><td>{{item.couponEffectiveEndTime}}</td>
				   	</tr>
				   	<tr>
				   		<td class="moban" style="width:100%;color:#E23030" colspan="14">
				   			<p><span v-html="item.title"></span></p>
				   			<p>{{item.reservePrice}},{{item.couponInfo}}</p>
				   			<p>: {{item.zkPrice}}</p>
				   			<p></p>
				   			<p>: <span v-if="item.couponLinkTaoToken">{{item.couponLinkTaoToken}}</span> <span v-else>{{item.taoToken}}</span> </p>
				   			<p>---------------------------</p>
				   			<p>1</p>
				   			<p>2</p>
				   			<p>315</p>
				   		</td>
				   	</tr>
				   	<tr>
				   		<td class="moban" style="width:100%" colspan="14">
				   			<button type="button" class="btn btn-success" @click="getyouhui(item.sellerId,item.auctionId)"></button>
				   		</td>
				   	</tr>
				   	<template v-if="item.sellerId == sellerId" >
				   		<template v-if="youhuidata.length">
				   			<tr v-for="list in youhuidata">
					   			<td class="moban" style="width:100%;color:#5454E6" colspan="14">
					   				<p><a v-bind:href="item.url">:{{list.ticketName}}</a></p>
					   				<p>:{{list.rest}}</p>
					   				<p>:{{list.count}}</p>
					   				<p>:{{list.titckExplain}}</p>
					   				<p>:{{list.titckDate}}</p>
					   				<p>:{{list.taoToken}}</p>
					   				<p>:{{list.url}}</p>
					   			</td>
						   	</tr>
				   		</template>
				   		<template v-else>
					   		<tr>
					   			<td class="moban" style="width:100%;color:#5454E6" colspan="14">
					   				
					   			</td>
					   		</tr>
				   		</template>
				   	</template>
			   	</tbody>
			   <!--</tbody>-->
			</table>
			<div @click = 'getmoregoods' class="more"></div>
		</template>
		<h1 style="text-align: center;margin-top:100px;" v-else></h1>
	</div>
	<loading v-bind:class="{loadingshow:isShow}"></loading>
  </div>
</template>
<script>
import {api} from '../assets/js/common';
import loading from '../components/loading'

export default {
	components: {
    loading
 },
  data () {
    return {
      goods:{},
      goodsdata:[],
      page:1,
      tagnames:[
      {tagname:"",name:""},
      {
      	tagname:"",name:"Gaoyon"},
      	{tagname:"",name:"Nzjh"},
      	{tagname:"",name:"Muying"},
      	{tagname:"99",name:"Channel9k9"},
      	{tagname:"20",name:"Channel20k"},
      	{tagname:"",name:"Tehui"}
      ],
      activeName:"",
      sortName:"",
      totalpage:'',
      theme:"",
      sortarray:[
      	{name:"",type:3},
      	{name:"",type:4},
      	{name:"",type:9},
      	{name:"",type:1},
      	{name:"",type:5},
      	{name:"",type:7},
      	{name:"",type:13},
      	{name:"",type:11},
      	{name:"",type:12}
      ],
      sortType:"",
      isShow:true,
      sellerId:"",
      youhuidata:[]
    }
  },
  methods:{
  	getRequest:function(_data){
  		this.isShow = false;
  		var self = this;
  		this.$http.post(api+'/index.php?r=search/search',_data).then((response) => {
  				var data = JSON.parse(response.body);
  				self.isShow = true;
  				console.log(data)
				if(data.state == 1000){
					if(self.page == 1){
						self.goodsdata=data.data.data;
						self.totalpage = data.data.totalpage;
					}
					else{
						for(var item in data.data.data){
							self.goodsdata.push(data.data.data[item]);
						}
					}				
					
					console.log(self.goodsdata)
				}
				else if(data.state == 1001)
			    {
			    		self.goodsdata = [];
			    }
		
		  }, (response) => {
		  	self.isShow = true;
		    // error callback
		  });
  		
  	},
  	getgoods:function(){
  		var self = this;
  		this.page = 1;
  		this.sortName = "";
  		this.sortType = "";
  		var _data = {
  			q:this.goods.goodsname,
  			page:this.page,
  			theme: this.theme
  		};
  		this.getRequest(_data);
  	},
  	getmoregoods:function(){
  		if(this.page == this.totalpage){
  			alert("");
  			return;
  		}
  		this.page++;
  		var self = this;
  		var _data = {
  			q:this.goods.goodsname,
  			page:this.page,
  			theme: this.theme,
  			sortType:this.sortType
  		};
  		this.getRequest(_data);
  	},
  	selected:function(tagname,name){
  		this.activeName=tagname;
  		this.theme = name;
		this.page = 1;
		var _data = {
			page:this.page,
			theme: this.theme,
			q:this.goods.goodsname
		}
		this.getRequest(_data)
  	},
  	sortselected:function(name,type){
  		this.sortName = name;
  		this.sortType = type;
  		this.page = 1;
  		var _data = {
  			q:this.goods.goodsname,
  			page:this.page,
  			theme: this.theme,
  			sortType:this.sortType
  		}
  		this.getRequest(_data)
  	},
  	getyouhui:function(sellerId,auctionId){
  		var self = this;
  		this.sellerId = sellerId;
  		this.$http.post(api+'/index.php?r=search/getticket',{sellerId:sellerId,auctionId:auctionId}).then((response) => {
  				var data = JSON.parse(response.body);
  				console.log(data)
				if(data.state == 1000){
					self.youhuidata = data.data;
				}
				else{
					self.youhuidata = [];
				}
		  }, (response) => {
		    // error callback
		  });
  	}
  },
  created(){
  	this.getgoods();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
th,td{max-width: 200px;white-space: normal;word-wrap: break-word;min-width: 80px;}
h1 {
  color: #42b983;
}
.moban{width:auto !important;}
.moban p{width:auto;}
.loadingshow{display: none;}
.active{color: #E80C39;border-color: #E80C39 !important;}
.goodscontent{margin-top:20px;}
.goodscontent table td img{width:120px;}
.goodstag{margin-top: 20px;}
.goodstag span{display: inline-block;padding:4px 6px;cursor:pointer;border-radius: 4px;border:1px solid #eee;font-size: 14px;margin:10px;}
.goodsort{margin-top: 10px;}
.goodsort ul li{display: inline-block;padding:4px 10px;font-size: 14px;cursor: pointer;margin-bottom: 10px;}
</style>



