<template>
  <div>
    <div class="am-form-group am-form-file" id="btnwrap2">
      <label type="button" class="btn btn-default">
        <i class="glyphicon glyphicon-open-file"></i> 
        <input type="file" id="qrcode_url2" class="hide" />
      </label>excel
    </div>
  </div>
</template>

<script>
import {api} from '../assets/js/common';
export default {
  data () {
    return {
      uptoken: ''
    }
  },
  methods:{
  	upload:function(a){
  		var self = this;
  		var uploader = Qiniu.uploader({
		  runtimes: 'html5,flash,html4', //,
		  browse_button: 'qrcode_url2', //****
		  uptoken: this.uptoken,
		  //AjaxupTokenUrl****
		  // uptoken : '<Your upload token>',
		  //uptoken_url, uptoken ,uptoken
		  // unique_names: true,
		  //  falsekeySDKkey
		  save_key: true,
		  //  falseuptoken `sava_key`SDKkey
		  domain: 'http://oe3slowqt.bkt.clouddn.com/',
		  //bucket ****
		  container: 'btnwrap2', //DOM IDbrowser_button
		  max_file_size: '5mb', //
		  flash_swf_url: 'qiniu/Moxie.swf', //flash,
		  max_retries: 3, //
		  dragdrop: true, //
		  drop_element: 'container', //ID
		  chunk_size: '4mb', //
		  auto_start: true, //
		  filters: {
		    mime_types: [
		        {title : "file files", extensions : "xls"} // flv
		    ]
		  },
		  multi_selection: false,
		  init: {
		    'FileUploaded': function(up, file, info) {
		      var domain = up.getOption('domain');
		      var res = JSON.parse(info);
		      var urlImg = 'http://oe3slowqt.bkt.clouddn.com/' + res.key;
		     	console.log(urlImg);
		     	self.exceladd(urlImg);
		    },
		    'Error': function(up, err, errTip) {
	               //
	               alert("")
	        }
		  }
		});
  	},
  	getuptoken:function(){
  		var self = this;
  		this.$http.get(api+'/index.php?r=search/token').then((response) => {
  			console.log(response.body);
  			var data = JSON.parse(response.body);
  			if(data.state == 1000){
  				self.uptoken = data.data;
  				self.upload();
  			}
  			else if(data.state == 1001){
  				alert(data.message);
  			}
  		}, (response) => {
		    // error callback
		  });
  	},
  	exceladd:function(excelUrl){
  		this.$http.post(api+'/index.php?r=data/exceladd',{excelUrl:excelUrl}).then((response) => {
  			console.log(response.body);
  			var data = JSON.parse(response.body);
  			if(data.state == 1000){
  				alert("")
  			}
  			else if(data.state == 1001){
  				alert(data.message);
  			}
  		}, (response) => {
		    // error callback
		  });
  	}
  },
  created(){
  	this.getuptoken();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1 {
  color: #42b983;
}
</style>



