<template>
  <div class="hello">
    <div class="am-form-group am-form-file" id="btnwrap">
      <label type="button" class="btn btn-default">
        <i class="glyphicon glyphicon-open-file"></i> 
        <input type="file" id="qrcode_url" class="hide" />
      </label>jpgpng
    </div>
    <div class="imgcode">
    		<img v-bind:src="imgurl" alt="" style="width: 200px; margin-top: 20px;"/>
    </div>
    <div class="form-horizontal" style="margin-top:10px">
	  <div class="form-group">
	    <label for="inputEmail3" class="col-sm-1 control-label"></label>
	    <div class="col-sm-6">
	      <input type="text" class="form-control" placeholder="" v-model="name">
	    </div>
	  </div>
	  <div class="form-group">
	    <div class="col-sm-offset-1 col-sm-10">
	      <button type="button" class="btn btn-default" @click="uploadcode"></button>
	    </div>
	  </div>
  </div>
    
  </div>
</template>

<script>
import {api} from '../assets/js/common';
export default {
  data () {
    return {
      uptoken: '',
      imgurl: "",
      name:""
    }
  },
  methods:{
  	upload:function(a){
  		var self = this;
  		var uploader = Qiniu.uploader({
		  runtimes: 'html5,flash,html4', //,
		  browse_button: 'qrcode_url', //****
		  uptoken: this.uptoken,
		  //AjaxupTokenUrl****
		  // uptoken : '<Your upload token>',
		  //uptoken_url, uptoken ,uptoken
		  // unique_names: true,
		  //  falsekeySDKkey
		  save_key: true,
		  //  falseuptoken `sava_key`SDKkey
		  domain: 'http://oe3slowqt.bkt.clouddn.com/',
		  //bucket ****
		  container: 'btnwrap', //DOM IDbrowser_button
		  max_file_size: '5mb', //
		  flash_swf_url: 'qiniu/Moxie.swf', //flash,
		  max_retries: 3, //
		  dragdrop: true, //
		  drop_element: 'container', //ID
		  chunk_size: '4mb', //
		  auto_start: true, //
		  filters: {
		    mime_types: [
		        {title : "Image files", extensions : "jpg,png"} // flv
		    ]
		  },
		  multi_selection: false,
		  init: {
		    'FileUploaded': function(up, file, info) {
		      var domain = up.getOption('domain');
		      var res = JSON.parse(info);
		      var urlImg = 'http://oe3slowqt.bkt.clouddn.com/' + res.key;
		     	console.log(urlImg);
				self.imgurl = urlImg;
		    },
		    'Error': function(up, err, errTip) {
	               //
	               alert("")
	        }
		  }
		});
  	},
  	getuptoken:function(){
  		var self = this;
  		this.$http.get(api+'/index.php?r=search/token').then((response) => {
  			console.log(response.body);
  			var data = JSON.parse(response.body);
  			if(data.state == 1000){
  				self.uptoken = data.data;
  				self.upload();
  			}
  			else if(data.state == 1001){
  				alert(data.message);
  			}
  		}, (response) => {
		    // error callback
		  });
  	},
  	uploadcode:function(){
  		var name = this.name;
  		var url = this.imgurl;
  		if(!url){
  			alert("");
  			return;
  		}
  		this.$http.post(api+':8818/liebian/add_qrcode_url',{url:url,name:name}).then((response) => {
  			console.log(response.body);
  			if(response.body.code == 0){
  				alert("")
  			}
  			
  		}, (response) => {
		    // error callback
		  });
  	}
  },
  created(){
  	this.getuptoken();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1 {
  color: #42b983;
}
</style>



